import React, { useState, useEffect, useRef } from 'react';
import '../../assets/scss/page/diet.scss';

function DietDiary() {
  const [diaryData, setDiaryData] = useState({
    date: new Date().toISOString().split('T')[0],
    sleep: '',
    condition: '',
    morningWeight: '',
    morningMeal: '',
    afternoonMeal: '',
    dinner: '',
    snack: '',
    afternoonWeight: '',
    memo: ''
  });

  const [diaryList, setDiaryList] = useState([]);
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
  const [viewMode, setViewMode] = useState('form'); // 'form' or 'list'
  const dateInputRef = useRef(null);
  const [showCalendar, setShowCalendar] = useState(false);
  const [currentMonth, setCurrentMonth] = useState(new Date());
  const calendarRef = useRef(null);

  //     
  useEffect(() => {
    const savedData = localStorage.getItem('dietDiary');
    if (savedData) {
      const parsedData = JSON.parse(savedData);
      //   ()
      const sortedData = [...parsedData].sort((a, b) => new Date(b.date) - new Date(a.date));
      setDiaryList(sortedData);
    }
  }, []);

  //      
  useEffect(() => {
    const savedData = localStorage.getItem('dietDiary');
    if (savedData) {
      const parsedData = JSON.parse(savedData);
      //     
      const todayData = parsedData.find(item => item.date === selectedDate);
      if (todayData) {
        setDiaryData(todayData);
      } else {
        //     
        setDiaryData({
          date: selectedDate,
          sleep: '',
          condition: '',
          morningWeight: '',
          morningMeal: '',
          afternoonMeal: '',
          dinner: '',
          snack: '',
          afternoonWeight: '',
          memo: ''
        });
      }
    } else {
      //    
      setDiaryData({
        date: selectedDate,
        sleep: '',
        condition: '',
        morningWeight: '',
        morningMeal: '',
        afternoonMeal: '',
        dinner: '',
        snack: '',
        afternoonWeight: '',
        memo: ''
      });
    }
  }, [selectedDate]);

  //    ( -   )
  const handleDateChange = (e) => {
    const newDate = e.target.value;
    setSelectedDate(newDate);
    // useEffect        
  };

  //    
  const handleDateTextChange = (e) => {
    const value = e.target.value;
    // YYYY-MM-DD  
    const dateRegex = /^\d{4}-\d{2}-\d{2}$/;
    if (value === '' || dateRegex.test(value)) {
      setSelectedDate(value);
    }
  };

  //  /
  const toggleCalendar = () => {
    setShowCalendar(!showCalendar);
    if (!showCalendar) {
      //       
      if (selectedDate) {
        setCurrentMonth(new Date(selectedDate));
      }
    }
  };

  //     
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (calendarRef.current && !calendarRef.current.contains(event.target) && 
          dateInputRef.current && !dateInputRef.current.contains(event.target)) {
        setShowCalendar(false);
      }
    };

    if (showCalendar) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showCalendar]);

  //   
  const handleDateSelect = (date) => {
    const dateStr = date.toISOString().split('T')[0];
    setSelectedDate(dateStr);
    setShowCalendar(false);
  };

  //   
  const changeMonth = (direction) => {
    setCurrentMonth(prev => {
      const newDate = new Date(prev);
      newDate.setMonth(prev.getMonth() + direction);
      return newDate;
    });
  };

  //   
  const getCalendarDays = () => {
    const year = currentMonth.getFullYear();
    const month = currentMonth.getMonth();
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    const daysInMonth = lastDay.getDate();
    const startingDayOfWeek = firstDay.getDay();

    const days = [];
    
    //    
    const prevMonth = new Date(year, month, 0);
    const prevMonthDays = prevMonth.getDate();
    for (let i = startingDayOfWeek - 1; i >= 0; i--) {
      days.push({
        date: new Date(year, month - 1, prevMonthDays - i),
        isCurrentMonth: false
      });
    }

    //   
    for (let day = 1; day <= daysInMonth; day++) {
      days.push({
        date: new Date(year, month, day),
        isCurrentMonth: true
      });
    }

    //     ( 42 )
    const remainingDays = 42 - days.length;
    for (let day = 1; day <= remainingDays; day++) {
      days.push({
        date: new Date(year, month + 1, day),
        isCurrentMonth: false
      });
    }

    return days;
  };

  //    
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setDiaryData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  //  input     
  const handleOtherInputFocus = (e) => {
    setShowCalendar(false);
  };

  //  input    
  const handleOtherInputMouseDown = (e) => {
    setShowCalendar(false);
  };

  //  
  const handleSave = () => {
    const updatedList = [...diaryList];
    const existingIndex = updatedList.findIndex(item => item.date === diaryData.date);
    
    if (existingIndex >= 0) {
      updatedList[existingIndex] = diaryData;
    } else {
      updatedList.push(diaryData);
    }
    
    //   ()
    updatedList.sort((a, b) => new Date(b.date) - new Date(a.date));
    
    setDiaryList(updatedList);
    localStorage.setItem('dietDiary', JSON.stringify(updatedList));
    alert('.');
  };

  //    
  const handleListItemClick = (date) => {
    setSelectedDate(date);
    setViewMode('form');
  };

  //  
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const weekdays = ['', '', '', '', '', '', ''];
    const weekday = weekdays[date.getDay()];
    return `${year}.${month}.${day} (${weekday})`;
  };

  //  
  const getConditionIcon = (condition) => {
    switch (condition) {
      case '':
        return '';
      case '':
        return '';
      case '':
        return '';
      case '':
        return '';
      case '':
        return '';
      default:
        return '';
    }
  };

  //  
  const handleDelete = () => {
    if (window.confirm(' ?')) {
      const updatedList = diaryList.filter(item => item.date !== selectedDate);
      setDiaryList(updatedList);
      localStorage.setItem('dietDiary', JSON.stringify(updatedList));
      
      setDiaryData({
        date: selectedDate,
        sleep: '',
        condition: '',
        morningWeight: '',
        morningMeal: '',
        afternoonMeal: '',
        dinner: '',
        snack: '',
        afternoonWeight: '',
        memo: ''
      });
      
      alert('.');
    }
  };

  return (
    <div className="diet-diary">
      <div className="com_center_wrap">
        <div className="diet-title-section">
          <h1 className="diet-title"> </h1>
          <div className="view-toggle">
            <button
              onClick={() => setViewMode('form')}
              className={viewMode === 'form' ? 'active' : ''}
            >
              
            </button>
            <button
              onClick={() => setViewMode('list')}
              className={viewMode === 'list' ? 'active' : ''}
            >
              
            </button>
          </div>
        </div>

        {viewMode === 'list' ? (
          <div className="diet-list-view">
            {diaryList.length === 0 ? (
              <div className="empty-state">
                <p>  .</p>
                <button onClick={() => setViewMode('form')} className="btn-add">
                    
                </button>
              </div>
            ) : (
              <div className="diary-items">
                {diaryList.map((item) => (
                  <div
                    key={item.date}
                    className="diary-item"
                    onClick={() => handleListItemClick(item.date)}
                  >
                    <div className="diary-item-header">
                      <h3 className="diary-date">{formatDate(item.date)}</h3>
                      {item.condition && (
                        <span className="condition-badge">
                          {getConditionIcon(item.condition)} {item.condition}
                        </span>
                      )}
                    </div>
                    <div className="diary-item-content">
                      <div className="diary-item-row">
                        {item.sleep && (
                          <div className="info-item">
                            <span className="label"></span>
                            <span className="value">{item.sleep}</span>
                          </div>
                        )}
                        {item.morningWeight && (
                          <div className="info-item">
                            <span className="label"> </span>
                            <span className="value">{item.morningWeight}kg</span>
                          </div>
                        )}
                        {item.afternoonWeight && (
                          <div className="info-item">
                            <span className="label"> </span>
                            <span className="value">{item.afternoonWeight}kg</span>
                          </div>
                        )}
                      </div>
                      {(item.morningMeal || item.afternoonMeal || item.dinner || item.snack) && (
                        <div className="meal-summary">
                          <span className="label">:</span>
                          {item.morningMeal && <span className="meal-tag"></span>}
                          {item.afternoonMeal && <span className="meal-tag"></span>}
                          {item.dinner && <span className="meal-tag"></span>}
                          {item.snack && <span className="meal-tag"></span>}
                        </div>
                      )}
                      {item.memo && (
                        <div className="memo-preview">
                          <span className="label">:</span>
                          <span className="memo-text">{item.memo.length > 50 ? item.memo.substring(0, 50) + '...' : item.memo}</span>
                        </div>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        ) : (
          <>
            <div className="diet-header">
              <div className="date-selector">
                <label htmlFor="date"> </label>
                <div className="date-input-wrapper">
                  <input
                    ref={dateInputRef}
                    type="text"
                    id="date"
                    name="date"
                    value={selectedDate}
                    onChange={handleDateTextChange}
                    onFocus={toggleCalendar}
                    placeholder="YYYY-MM-DD"
                    className="date-input"
                    readOnly
                  />
                  <button 
                    type="button" 
                    className="calendar-toggle-btn"
                    onClick={toggleCalendar}
                  >
                    
                  </button>
                  {showCalendar && (
                    <div ref={calendarRef} className="calendar-popup">
                      <div className="calendar-header">
                        <button 
                          type="button" 
                          className="calendar-nav-btn"
                          onClick={() => changeMonth(-1)}
                        >
                          
                        </button>
                        <span className="calendar-month">
                          {currentMonth.getFullYear()} {currentMonth.getMonth() + 1}
                        </span>
                        <button 
                          type="button" 
                          className="calendar-nav-btn"
                          onClick={() => changeMonth(1)}
                        >
                          
                        </button>
                      </div>
                      <div className="calendar-weekdays">
                        <div className="calendar-weekday"></div>
                        <div className="calendar-weekday"></div>
                        <div className="calendar-weekday"></div>
                        <div className="calendar-weekday"></div>
                        <div className="calendar-weekday"></div>
                        <div className="calendar-weekday"></div>
                        <div className="calendar-weekday"></div>
                      </div>
                      <div className="calendar-days">
                        {getCalendarDays().map((day, index) => {
                          const dateStr = day.date.toISOString().split('T')[0];
                          const isSelected = dateStr === selectedDate;
                          const isToday = dateStr === new Date().toISOString().split('T')[0];
                          
                          return (
                            <button
                              key={index}
                              type="button"
                              className={`calendar-day ${!day.isCurrentMonth ? 'other-month' : ''} ${isSelected ? 'selected' : ''} ${isToday ? 'today' : ''}`}
                              onClick={() => handleDateSelect(day.date)}
                              disabled={!day.isCurrentMonth}
                            >
                              {day.date.getDate()}
                            </button>
                          );
                        })}
                      </div>
                    </div>
                  )}
                </div>
              </div>
              <div className="diet-actions">
                <button onClick={handleSave} className="btn-save"></button>
                <button onClick={handleDelete} className="btn-delete"></button>
              </div>
            </div>

        <div className="diet-form">
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="sleep">  ()</label>
              <input
                type="number"
                id="sleep"
                name="sleep"
                value={diaryData.sleep}
                onChange={handleInputChange}
                onFocus={handleOtherInputFocus}
                onMouseDown={handleOtherInputMouseDown}
                placeholder=": 7.5"
                step="0.5"
                min="0"
                max="24"
              />
            </div>
            <div className="form-group">
              <label htmlFor="condition"></label>
              <select
                id="condition"
                name="condition"
                value={diaryData.condition}
                onChange={handleInputChange}
                onFocus={handleOtherInputFocus}
                onMouseDown={handleOtherInputMouseDown}
              >
                <option value=""></option>
                <option value=""> </option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""> </option>
              </select>
            </div>
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="morningWeight">  (kg)</label>
              <input
                type="number"
                id="morningWeight"
                name="morningWeight"
                value={diaryData.morningWeight}
                onChange={handleInputChange}
                onFocus={handleOtherInputFocus}
                onMouseDown={handleOtherInputMouseDown}
                placeholder=": 65.5"
                step="0.1"
                min="0"
              />
            </div>
            <div className="form-group">
              <label htmlFor="afternoonWeight">  (kg)</label>
              <input
                type="number"
                id="afternoonWeight"
                name="afternoonWeight"
                value={diaryData.afternoonWeight}
                onChange={handleInputChange}
                onFocus={handleOtherInputFocus}
                onMouseDown={handleOtherInputMouseDown}
                placeholder=": 66.2"
                step="0.1"
                min="0"
              />
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="morningMeal"> </label>
            <textarea
              id="morningMeal"
              name="morningMeal"
              value={diaryData.morningMeal}
              onChange={handleInputChange}
              onFocus={handleOtherInputFocus}
              onMouseDown={handleOtherInputMouseDown}
              placeholder="   "
              rows="3"
            />
          </div>

          <div className="form-group">
            <label htmlFor="afternoonMeal"> </label>
            <textarea
              id="afternoonMeal"
              name="afternoonMeal"
              value={diaryData.afternoonMeal}
              onChange={handleInputChange}
              onFocus={handleOtherInputFocus}
              onMouseDown={handleOtherInputMouseDown}
              placeholder="   "
              rows="3"
            />
          </div>

          <div className="form-group">
            <label htmlFor="dinner"> </label>
            <textarea
              id="dinner"
              name="dinner"
              value={diaryData.dinner}
              onChange={handleInputChange}
              onFocus={handleOtherInputFocus}
              onMouseDown={handleOtherInputMouseDown}
              placeholder="   "
              rows="3"
            />
          </div>

          <div className="form-group">
            <label htmlFor="snack"></label>
            <textarea
              id="snack"
              name="snack"
              value={diaryData.snack}
              onChange={handleInputChange}
              onFocus={handleOtherInputFocus}
              onMouseDown={handleOtherInputMouseDown}
              placeholder="  "
              rows="3"
            />
          </div>

          <div className="form-group">
            <label htmlFor="memo"></label>
            <textarea
              id="memo"
              name="memo"
              value={diaryData.memo}
              onChange={handleInputChange}
              onFocus={handleOtherInputFocus}
              onMouseDown={handleOtherInputMouseDown}
              placeholder="  "
              rows="4"
            />
          </div>
        </div>
          </>
        )}
      </div>
    </div>
  );
}

export default DietDiary;

